window.customElements.define('ui-firebase', class extends HTMLElement {
    constructor() {
        super();
        this._userdata = {};
        this.start();
        this.userdatachanged_cb = (event) => this.userdatachanged(event.detail);
    }
    async start() {
        let module = await import("/js/cmp/userdata.js");
        this._userdata = module.userdata;
        this._userdata.addEventListener("data", this.userdatachanged_cb);
        if (this._userdata.data) this.userdatachanged(this._userdata.data);
    }
    disconnectCallback() {
        if (this._userdata)
            this._userdata.removeEventListener("data", this.userdatachanged_cb);
    }
    userdatachanged(data) {
        this.dispatchEvent(new CustomEvent("data", { detail: data }));
    }
    get data() {
        if (this._userdata)
            return this._userdata.data;
        return null;
    }
    get user() {
        if (this._userdata)
            return this._userdata.user;
    }
});